<template lang="html">
  <div class="position-page">
    <default-head header="定位"></default-head>
    <light-title text="当前定位地址"></light-title>
    <fix-position @get-near-shop="getNearShop"></fix-position>
    <light-title text="附近地址"></light-title>
    <div class="position-page__list">
      <position-list v-for="item in addArr" :key="item.index" :text="item.title" @click.native="saveLocation(item)"></position-list>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import minutils from '@/utils/minUtils'
import utils from '@/utils/utils'
import defaultHead from '@/components/head/defaultHead'
import fixPosition from '@/components/position/fixPosition'
import positionList from '@/components/position/positionList'
import lightTitle from '@/components/common/lightTitle'

export default {
  data () {
    return {
      addArr: []
    }
  },
  components: { fixPosition, positionList, defaultHead, lightTitle },
  computed: {
    ...mapGetters({location: 'getLocation'})
  },
  methods: {
    getNearShop (location) {
      setTimeout(function () {
        minutils.IndicatorClose()
      }, 3000)
      this.ajaxGetNearShop(function () {
        // 获取结束关闭弹窗
        minutils.IndicatorClose()
      })
    },
    ajaxGetNearShop (callback) {
      // address.city + address.district + address.street + address.streetNumber
      var map = new BMap.Map()
      let data = this.location

      utils.searchComplete(map, data.city + data.district + data.street + data.streetNumber, this.addArr = [], callback)
    },
    saveLocation (item) {
      // 存储到store
      let data = {
        city: item.city,
        district: item.district,
        lat: item.point.lat,
        lng: item.point.lng,
        province: item.province,
        street: item.title,
        streetNumber: ''
      }
      this.$store.dispatch('setLocation', data)
      this.$router.push('/search')
    }
  }
}
</script>

<style lang="less" scoped>
@import '../assets/css/variable.less';

.position-page {
  padding-top: .88rem;
  .position-page__now {
    font-size: .28rem;
    line-height: .28rem;
    padding: .44rem .22rem .2rem;
    color: @lightColor;
  }
}
</style>



