<template lang="html">
  <div class="fix-position clearfix">
    <span @click="$router.push('/cardsearchaddress')" class="fl fix-position__left"><i class="search__icon"></i>{{current}}</span>
    <span @click="getCurrentLocation(getCurrent)" class="fr fix-position__right activeColor"><i class="position__icon"></i>重新定位</span>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import utils from '@/utils/utils'
import minutils from '@/utils/minUtils'
export default {
  data () {
    return {
      current: ''
    }
  },
  mounted () {
    this.initPage()
  },
  computed: {
    ...mapGetters({location: 'getLocation'})
  },
  methods: {
    initPage () {
      if ('street' in this.location) {
        minutils.IndicatorOpen('加载中...')
        this.getCurrent(this.location)
      } else {
        this.getCurrentLocation(this.getCurrent)
      }
    },
    // 获取当前位置
    getCurrentLocation (callback) {
      minutils.IndicatorOpen('加载中...')
      utils.getCurrentLocation(callback)
    },
    getCurrent (location) {
      // 将当前位置存放到store
      this.$store.dispatch('setLocation', location)
      this.current = location.street
      // 此处调用父类方法, 在该方法的异步方法中关闭加载动画
      this.$emit('get-near-shop', location)
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../assets/css/variable.less';
@import '../../assets/css/property.less';

.fix-position {
  font-size: .32rem;
  padding: 0 .22rem;
  height: .88rem;
  background-color: @whiteColor;
  .fix-position__left {
    width: 5.2rem;
    margin-top: .1rem;
    height: .68rem;
    padding-left: .6rem;
    line-height: .68rem;
    background: #f1f1f1;
    position: relative;
    overflow: hidden;
    color: @lightColor;
    .search__icon {
      top: .12rem;
      left: .1rem;
    }
  }
  .fix-position__right {
    padding-top: .28rem;
    position: relative;
    .position__icon {
      left: -.52rem;
      top: .22rem;
    }
  }
}
</style>



